.header {
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 146px;
	}
	&__logo {
		position: relative;
		overflow: hidden;
		width: 147px;
		height: 56px;
		& img {
			position: absolute;
			width: 100%;
			height: 100%;
		}
	}
	&__action {
		display: flex;
		align-items: center;
	}
	// &__btn {
		
	// }
	&__phone {
		margin: 0 34px 0 53px;
		& a {
			display: inline-block;
			width: 100%;
			height: 100%;
			font-weight: 600;
			font-size: 20px;
			line-height: 24px;
			color: #0f0f0f;
		}
	}
	&__map {
		cursor: pointer;
		position: relative;
		background: #ffffff;
		height: 56px;
		width: 56px;
		border-radius: 50%;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		& img {
			position: absolute;
			width: 27px;
			height: 33px;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}

.intro {
	display: flex;
	justify-content: space-between;
	&__text-block {
		flex: 0 0 535px;
		margin-right: 60px;
	}
	&__text-wrap {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
	}
	&__title {
		font-weight: 800;
		font-size: 80px;
		line-height: 90px;
		color: $base;
	}
	&__descr {
		padding: 38px 0 60px 0;
		font-weight: 400;
		font-size: 28px;
		line-height: 34px;
		color: #808080;
	}
	&__btn {
		align-self: flex-start
	}
	&__img-block {
		flex: 0 0 50%;
		position: relative;
		& img{
		
			position: absolute;
			width: 100%;
			height: 100%;
			bottom: 0;
			right: 0;
			object-fit: contain;
		}
	}
}
