	}
	&__title {
		margin: 9px 0 10px 0;
		font-weight: 700;
		font-size: 16px;
		line-height: 20px;
		color: #000000;
	}
	&__descr {
		font-weight: 400;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		color: #808080;
		text-align: center;
	}
}
