// dots-sliderdots-sliderdots-sliderdots-sliderdots-sliderdots-sliderdots-sliderdots-slider
.swiper-container__dots-slider {
	max-width: 725px;
	overflow: hidden;
	margin: 70px auto 60px;
	text-align: center;
}

.swiper-container__dots-slider .swiper-wrapper {
	justify-content: space-between;
}

.dots-slide {
	width: 45px;
	cursor: pointer;
	&__dot {
		position: relative;
		margin: 0 auto;
		width: 32px;
		height: 32px;
		background: #c2c2c2;
		border-radius: 50%;
		&::before {
			content: "";
			position: absolute;
			width: 85px;
			height: 4px;
			background: #c2c2c2;
			top: 50%;
			right: 16px;
			transform: translateY(-50%);
		}
		&::after {
			content: "";
			position: absolute;
			width: 85px;
			height: 4px;
			background: #c2c2c2;
			top: 50%;
			left: 16px;
			transform: translateY(-50%);
		}
		&_first {
			&::before {
				display: none;
			}
		}
		&_last {
			&::after {
				display: none;
			}
		}
	}

	&__number {
		font-weight: 500;
		font-size: 33px;
		line-height: 41px;
		color: #c2c2c2;
	}
}

.swiper-container__dots-slider .swiper-slide-thumb-active .dots-slide__dot {
	background: #545454;
	box-shadow: 0px 2.47469px 10.7237px rgba(153, 153, 153, 0.5);
	border-radius: 96.7245px;
	&::before,
	&::after {
		background: #545454;
	}
}

.swiper-container__dots-slider .swiper-slide-thumb-active .dots-slide__number {
	font-weight: 400;
	font-size: 33px;
	line-height: 41px;
	color: #545454;
}
