.swiper-container__stage-slider {
	overflow: hidden;
	min-height: 340px;
	padding: 40px;
	background: #ffffff;
	box-shadow: 0px 2.89522px 12.546px rgba(176, 176, 176, 0.35);
	border-radius: 19.3015px;
}

.stage-slide {
	display: flex;
	&__text-block {
		margin-right: 27px;
		margin-bottom: 25px;
		max-width: 370px;
	}
	&__title {
		font-weight: 600;
		font-size: 28px;
		line-height: 34px;
		color: #000000;
		margin-bottom: 24px;
	}
	&__text {
		font-weight: 400;
		font-size: 20px;
		line-height: 24px;
		color: #808080;
	}
	&__img {
		position: relative;
		margin-left: 27px;
		width: 360px;
		height: 220px;
		& picture img {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.swiper-container__stage-slider .swiper-button-next {
	position: relative;
	text-align-last: left;
	width: 274px;
	font-weight: 600;
	font-size: 20px;
	line-height: 24px;
	color: #545454;
	background-color: #fff;
	&::after {
		content: "";
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 70px;
		height: 20px;
		background-image: url(../img/icons/arrows-next-stage.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
	}
}

