@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Light.woff") format("woff"), url("../fonts/Montserrat-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  transition: all 0s ease 0s;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Montserrat";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "Montserrat";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

body {
  color: #0f0f0f;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  background-color: #efefef;
}

body._lock {
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

._container {
  max-width: 1324px;
  padding: 0 15px;
  margin: 0 auto;
}

.btn {
  padding: 16px 32px;
  box-shadow: 0px 3px 13px rgba(153, 153, 153, 0.5);
  border-radius: 100px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #ffffff;
}

.btn_white {
  background: #ffffff;
}

.btn_grey {
  background: #545454;
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 146px;
}

.header__logo {
  position: relative;
  overflow: hidden;
  width: 147px;
  height: 56px;
}

.header__logo img {
  position: absolute;
  width: 100%;
  height: 100%;
}

.header__action {
  display: flex;
  align-items: center;
}

.header__phone {
  margin: 0 34px 0 53px;
}

.header__phone a {
  display: inline-block;
  width: 100%;
  height: 100%;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #0f0f0f;
}

.header__map {
  cursor: pointer;
  position: relative;
  background: #ffffff;
  height: 56px;
  width: 56px;
  border-radius: 50%;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
}

.header__map img {
  position: absolute;
  width: 27px;
  height: 33px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.intro {
  display: flex;
  justify-content: space-between;
}

.intro__text-block {
  flex: 0 0 535px;
  margin-right: 60px;
}

.intro__text-wrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.intro__title {
  font-weight: 800;
  font-size: 80px;
  line-height: 90px;
  color: #0f0f0f;
}

.intro__descr {
  padding: 38px 0 60px 0;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  color: #808080;
}

.intro__btn {
  align-self: flex-start;
}

.intro__img-block {
  flex: 0 0 50%;
  position: relative;
}

.intro__img-block img {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  right: 0;
  object-fit: contain;
}

.advantage {
  margin-top: 100px;
  margin-bottom: 100px;
}

.advantage__wrapp {
  display: flex;
  justify-content: space-between;
  margin: 0 -30px;
  max-width: 1175px;
  margin: 0 auto;
}

.advantage__item {
  flex: 0 1 246px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 246px;
  min-height: 246px;
  padding: 32px;
  margin: 0 30px;
  background: #ffffff;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
  border-radius: 25px;
}

.advantage__img {
  position: relative;
  height: 120px;
  width: 120px;
}

.advantage__img img {
  position: absolute;
  object-fit: contain;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.advantage__title {
  margin: 9px 0 10px 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
}

.advantage__descr {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #808080;
  text-align: center;
}

.modifier__img {
  margin: 40px 0;
  padding-bottom: 28%;
  position: relative;
  width: 100%;
}

.modifier__img img {
  position: absolute;
  left: 0;
  height: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.modifier__result {
  margin-top: 90px;
}

.modifier__wall .config__item {
  margin-left: 0;
  margin-right: 25px;
}

.config__wrapp {
  display: flex;
}

.config__wrapp_roof {
  justify-content: end;
}

.config__item {
  display: flex;
  align-items: center;
  max-width: 290px;
  border-radius: 128px;
  padding: 13px 0;
  margin-left: 25px;
  transition: all 0.2s ease-in-out;
}

.config__item_wall {
  flex-direction: row-reverse;
}

.config__text-block {
  text-align: right;
  max-width: 152px;
  margin-right: 21px;
}

.config__text-block_revers {
  text-align: right;
}

.config__title {
  font-weight: 500;
  font-size: 0px;
  opacity: 0%;
  line-height: 19px;
  color: #545454;
  margin-bottom: 8px;
  transition: all 0.2s ease-in-out;
}

.config__descr {
  font-weight: 400;
  font-size: 0px;
  opacity: 0%;
  line-height: 16px;
  color: #999999;
  transition: all 0.2s ease-in-out;
}

.config__item:hover {
  padding: 13px 21px;
  background: #ffffff;
  box-shadow: 0px 3.81789px 16.5442px rgba(176, 176, 176, 0.35);
}

.config__item:hover .config__title {
  font-size: 16px;
  opacity: 100%;
}

.config__item:hover .config__descr {
  font-size: 14px;
  opacity: 100%;
}

.result__wrap {
  display: flex;
  margin: 0 -25px;
}

.result__item {
  padding: 40px 53px;
  margin: 0 25px;
  display: flex;
  flex: 0 1 50%;
  background: #ffffff;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
  border-radius: 50.9051px;
}

.result__item_rewers {
  flex-direction: row-reverse;
}

.result__img {
  position: relative;
  width: 127px;
  height: 127px;
  border-radius: 50%;
  overflow: hidden;
}

.result__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.result__text-block {
  max-width: 355px;
  margin-left: 40px;
}

.result__text-block_rewers {
  margin-left: 0px;
  margin-right: 40px;
}

.result__title {
  font-weight: 600;
  font-size: 20px;
  line-height: 25px;
}

.result__descr {
  margin-top: 10px;
  font-weight: 400;
  font-size: 17px;
  line-height: 22px;
  color: #999999;
}

.stage__wrap {
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
}

.stage__left-block {
  flex: 0 0 302px;
  padding: 76px 32px 64px 32px;
  background: #ffffff;
  box-shadow: 0px 2.89522px 12.546px rgba(176, 176, 176, 0.35);
  border-radius: 19.3015px;
  margin-right: 25px;
  text-align: center;
}

.stage__right-block {
  flex: 0 1 880px;
  margin-left: 25px;
  max-width: 880px;
}

.left-block__img {
  position: relative;
  margin: 0 auto;
  width: 175px;
  height: 195px;
  margin-bottom: 45px;
}

.left-block__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.left-block__descr {
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
  text-align: center;
  color: #808080;
}

.swiper-container__dots-slider {
  max-width: 725px;
  overflow: hidden;
  margin: 70px auto 60px;
  text-align: center;
}

.swiper-container__dots-slider .swiper-wrapper {
  justify-content: space-between;
}

.dots-slide {
  width: 45px;
  cursor: pointer;
}

.dots-slide__dot {
  position: relative;
  margin: 0 auto;
  width: 32px;
  height: 32px;
  background: #c2c2c2;
  border-radius: 50%;
}

.dots-slide__dot::before {
  content: "";
  position: absolute;
  width: 85px;
  height: 4px;
  background: #c2c2c2;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
}

.dots-slide__dot::after {
  content: "";
  position: absolute;
  width: 85px;
  height: 4px;
  background: #c2c2c2;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
}

.dots-slide__dot_first::before {
  display: none;
}

.dots-slide__dot_last::after {
  display: none;
}

.dots-slide__number {
  font-weight: 500;
  font-size: 33px;
  line-height: 41px;
  color: #c2c2c2;
}

.swiper-container__dots-slider .swiper-slide-thumb-active .dots-slide__dot {
  background: #545454;
  box-shadow: 0px 2.47469px 10.7237px rgba(153, 153, 153, 0.5);
  border-radius: 96.7245px;
}

.swiper-container__dots-slider .swiper-slide-thumb-active .dots-slide__dot::before,
.swiper-container__dots-slider .swiper-slide-thumb-active .dots-slide__dot::after {
  background: #545454;
}

.swiper-container__dots-slider .swiper-slide-thumb-active .dots-slide__number {
  font-weight: 400;
  font-size: 33px;
  line-height: 41px;
  color: #545454;
}

.swiper-container__stage-slider {
  overflow: hidden;
  min-height: 340px;
  padding: 40px;
  background: #ffffff;
  box-shadow: 0px 2.89522px 12.546px rgba(176, 176, 176, 0.35);
  border-radius: 19.3015px;
}

.stage-slide {
  display: flex;
}

.stage-slide__text-block {
  margin-right: 27px;
  margin-bottom: 25px;
  max-width: 370px;
}

.stage-slide__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 34px;
  color: #000000;
  margin-bottom: 24px;
}

.stage-slide__text {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #808080;
}

.stage-slide__img {
  position: relative;
  margin-left: 27px;
  width: 360px;
  height: 220px;
}

.stage-slide__img picture img {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-container__stage-slider .swiper-button-next {
  position: relative;
  text-align-last: left;
  width: 274px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #545454;
  background-color: #fff;
}

.swiper-container__stage-slider .swiper-button-next::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 70px;
  height: 20px;
  background-image: url(../img/icons/arrows-next-stage.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.lorem {
  margin-top: 50px;
}

.lorem__wrap {
  display: flex;
  align-items: center;
}

.lorem__img-bloc {
  padding: 20px 42px;
  flex: 0 0 560px;
  min-height: 562px;
  background: #ffffff;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
  border-radius: 20px;
  margin-right: 26px;
}

.lorem__img {
  position: relative;
  min-width: 560px;
  min-height: 562px;
  overflow: hidden;
}

.lorem__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: contain;
}

.lorem__text-block {
  margin-left: 26px;
}

.lorem__title {
  font-weight: 600;
  font-size: 40px;
  line-height: 49px;
  color: #000000;
  margin-bottom: 35px;
}

.lorem__text {
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  margin-bottom: 55px;
}

.lorem__btn {
  position: relative;
  width: 312px;
  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
  text-align: left;
  color: #545454;
}

.lorem__btn::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 70px;
  height: 20px;
  background-image: url(../img/icons/arrows-next-stage.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

footer {
  height: 200px;
}